<template>
    <div class="container mt-5 mb-5" style="max-width: 500px; text-align: left">
      <div class="alert alert-success" role="alert">
        <h2 class="alert-heading text-center">Đăng nhập</h2>
      </div>

      <form @submit.prevent="handleSubmit">
        <div class="form-group">
          <label for="email">Email:</label>
          <input
            type="email"
            v-model="userForm.email"
            id="email"
            name="email"
            class="form-control"
            :class="{ 'is-invalid': isSubmitted && $v.userForm.email.$error }"
          />
          <div
            v-if="isSubmitted && $v.userForm.email.$error"
            class="invalid-feedback"
          >
            <span v-if="!$v.userForm.email.required"
              >Email không được bỏ trống</span
            >
            <span v-if="!$v.userForm.email.email"
              >Vui lòng nhập đúng định dạng email</span
            >
          </div>
        </div>

        <div class="form-group mt-3">
          <label for="password">Mật khẩu:</label>
          <input
            type="password"
            v-model="userForm.password"
            id="password"
            name="password"
            class="form-control"
            :class="{
              'is-invalid': isSubmitted && $v.userForm.password.$error,
            }"
          />
          <div
            v-if="isSubmitted && $v.userForm.password.$error"
            class="invalid-feedback"
          >
            <span v-if="!$v.userForm.password.required"
              >Mật khẩu không được bỏ trống</span
            >
            <span v-if="!$v.userForm.password.minLength"
              >Mật khẩu ít nhất 8 ký tự</span
            >
          </div>
        </div>
        <div class="form-group mt-3 ">
          <button  class="btn-login btn btn-info btn-block w-100">Đăng nhập</button>
        </div>
      </form>
      <div class="alert">
        Nếu chưa có tài khoản hãy nhấn vào đây ->
         <router-link to="/register" class="text-decoration-none h6">Tạo tài khoản</router-link>
      </div>
    </div>
</template>
<script>
import { required, email, minLength } from "vuelidate/lib/validators";
import { mapGetters, mapActions, mapMutations } from "vuex";
export default {
  name: "Login",
  data() {
    return {
      userForm: {
        email: "",
        password: "",
      },
      isSubmitted: false,
    };
  },
   validations: {
    userForm: {
      email: {
        required,
        email,
      },
      password: {
        required,
        minLength: minLength(8),
      }
    },
  },
  computed: {
    ...mapGetters(['loginInfo'])
  },
  methods: {
    handleSubmit() {
      this.isSubmitted = true;
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      this.loginAccount();
    },
    ...mapMutations(['getLoginInfo']),
    ...mapActions(['loginAccount'])
  },
};
</script>

<style>
.form-group > label {
  font-weight: 600;
}
</style>